import ky from "ky";
import { WeeklyDigest } from "./types/weekly_digest";
import { DateTime } from "luxon";

const handball4all = ky.create({
  prefixUrl: "https://spo.handball4all.de/service/"
});

export class ApiClient {
  public constructor(private club: number) {}

  public async getWeeklyDigest(offset: number = 0): Promise<WeeklyDigest> {
    const startOfWeek = DateTime.local()
      .startOf("week")
      .plus({ week: offset })
      .toFormat("yyyy-LL-dd");

    const url = `if_g_json.php?c=${this.club}&cmd=pcu&do=${startOfWeek}`;
    const response = await handball4all.get(url);
    const [json] = await response.json();
    return json;
  }
}
