import { Head } from "../api/types/head";
import { Age } from "../api/types/age";
import { Gender } from "../api/types/gender";

export function isHomeTeam(team: string, clubName: string) {
  return team.indexOf(clubName) === 0;
}

export function formatTeamName(
  clubName: string,
  age: Age,
  gender: Gender,
  team: string
) {
  if (!isHomeTeam(team, clubName)) {
    return team;
  }
  return getAgeName(age, gender);
}

export function getAgeName(age: Age, gender: Gender) {
  if (gender === "g") {
    return "Minis";
  }

  if (gender === "w") {
    if (age === "F") {
      return "Frauen";
    }
    return `${age}-Mdchen`;
  }

  if (gender === "m") {
    if (age === "M") {
      return "Mnner";
    }
    return `${age}-Jungen`;
  }
}
