import * as React from "react";
import { render } from "react-dom";
import { WeeklyDigest } from "./components/WeeklyDigest";
import "./styles.css";

const attributes = {
  clubId: "h4u-club-id",
  weekOffset: "h4u-week-offset",
  filter: "h4u-filter"
};

const selectors = {
  weeklyDigest: "[h4u-weekly-digest]"
};

const init = () => {
  document.querySelectorAll(selectors.weeklyDigest).forEach(dom => {
    const clubId = dom.getAttribute(attributes.clubId);
    const weekOffset = dom.getAttribute(attributes.weekOffset);
    const filter = dom.getAttribute(attributes.filter) as
      | "home"
      | "outwards"
      | undefined;

    if (!clubId) {
      console.error("missing attribute %s", attributes.clubId);
    }

    render(
      React.createElement(WeeklyDigest, {
        clubId: Number(clubId),
        weekOffset: weekOffset ? Number(weekOffset) : undefined,
        filter
      }),
      dom
    );
  });
};

export default init;
