import * as React from "react";
import { Game } from "../api/types/game";
import { Class } from "../api/types/class";
import { formatTeamName, isHomeTeam } from "../format/team_name";
import { Result } from "./Result";

interface Props {
  game: Game;
  matchClass: Class;
  clubName: string;
  className?: string;
}

export const GameRow: React.FunctionComponent<Props> = props => {
  const { game, matchClass, clubName, className } = props;
  const isHome = isHomeTeam(game.gHomeTeam, clubName);

  const teamName = (name: string) => {
    const formatted = formatTeamName(
      clubName,
      matchClass.gClassAGsDesc,
      matchClass.gClassGender,
      name
    );

    if (isHomeTeam(name, clubName)) {
      return <strong>{formatted}</strong>;
    }

    return formatted;
  };

  const {
    gWDay,
    gDate,
    gTime,
    gGymnasiumName,
    gGuestGoals,
    gGuestGoals_1,
    gHomeGoals,
    gHomeGoals_1,
    gHomeTeam,
    gGuestTeam
  } = game;

  const hasGameResult = gHomeGoals_1.trim() !== "";

  return (
    <div className={`matchcontainer ${className || ""}`}>
      <div className={isHome ? "home" : "outwards"}>
        <p className="dateandhall">
          {gWDay}, {hasGameResult ? gDate : gTime}
          <br />
          <span className="hallname">{gGymnasiumName}</span>{" "}
        </p>
        <p className="match">
          {teamName(gHomeTeam)}
          &nbsp;-&nbsp; {teamName(gGuestTeam)}
        </p>
        <div className="rslt">
          <span className="hlf">
            {hasGameResult && (
              <>
                (<Result goalsHome={gHomeGoals_1} goalsGuest={gGuestGoals_1} />){" "}
              </>
            )}
          </span>
          <b>
            <Result goalsHome={gHomeGoals} goalsGuest={gGuestGoals} />
          </b>
        </div>
        <div className="clearfix" />
      </div>
    </div>
  );
};
