import * as React from "react";
import { ApiClient } from "../api/client";
import { useWeeklyDigest } from "../hooks/weekly_digest";
import { GameRow } from "./GameRow";
import { WeeklyDigestLoader } from "./WeeklyDigestLoader";
import { Game } from "../api/types/game";
import { isHomeTeam } from "../format/team_name";

interface Props {
  clubId: number;
  weekOffset?: number;
  filter?: "home" | "outwards";
}

export const WeeklyDigest: React.FunctionComponent<Props> = props => {
  const { clubId, weekOffset, filter } = props;
  const client = new ApiClient(clubId);
  const data = useWeeklyDigest(client, weekOffset);

  if (!data) {
    return <WeeklyDigestLoader />;
  }

  const filterGame = (game: Game) => {
    if (!filter) {
      return true;
    }

    const isHome = isHomeTeam(game.gHomeTeam, data.head.sname);

    if (filter === "home") {
      return isHome;
    }
    if (filter === "outwards") {
      return !isHome;
    }

    return true;
  };

  let count = 0;

  const rows = data.content.classes.map(c => (
    <React.Fragment key={c.gClassID}>
      {c.games.filter(filterGame).map(g => (
        <GameRow
          className={++count % 2 === 0 ? "even" : "odd"}
          key={g.gID}
          game={g}
          clubName={data.head.sname}
          matchClass={c}
        />
      ))}
    </React.Fragment>
  ));
  return <>{rows}</>;
};
