import * as React from "react";
import { Game } from "../api/types/game";
import { Class } from "../api/types/class";
interface Props {
  goalsHome: string;
  goalsGuest: string;
}

export const Result: React.FunctionComponent<Props> = props => {
  const goalsHome = props.goalsHome.trim();
  const goalsGuest = props.goalsGuest.trim();
  const resultString = `${goalsHome || "0"}:${goalsGuest || "0"}`;
  return <>{resultString}</>;
};
