import { ApiClient } from "../api/client";
import { useState, useEffect } from "react";
import { WeeklyDigest } from "../api/types/weekly_digest";

export const useWeeklyDigest = (
  apiClient: ApiClient,
  weekOffset?: number
): WeeklyDigest | undefined => {
  const [data, setData] = useState<WeeklyDigest | undefined>();

  useEffect(() => {
    const getData = async () => {
      const weeklyDigest = await apiClient.getWeeklyDigest(weekOffset);
      setData(weeklyDigest);
    };
    getData();
  }, []);

  return data;
};
